.topbar{
    width:100%;
    height:50px;
    background-color:teal;
    position: sticky;
    top: 0;
    z-index: 999;
}

.topbarWrapper{
    height: 100%;
    padding: 0px 20px;
    display: flex;
    align-items: center;
    justify-content: space-between;
}

.logo {
    font-weight: bold;
    font-size: 30px;
    color:white;
    cursor:pointer
}
.top-right {
    display: flex;
    align-items: center;
}
.topbarIconsContainer{
    position: relative;
    cursor: pointer;
    margin-right: 10px;
}

.topAvatar{
   width: 40px;
   height: 40px;
   border-radius: 50%;
   cursor: pointer; 
}